﻿import { Component, OnInit, Inject, ViewChild, AfterContentInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { Subject } from 'rxjs';
import { debounceTime, finalize, switchMap } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import { OfficeService } from './services/office.service';
import { Office } from './office.model';
import { NhTabComponent } from '../../../../shareds/components/nh-tab/nh-tab.component';
import { BaseListComponent } from '../../../../base-list.component';
import { IPageId, PAGE_ID } from '../../../../configs/page-id.config';
import { SpinnerService } from '../../../../core/spinner/spinner.service';
import { UtilService } from '../../../../shareds/services/util.service';
import { AppService } from '../../../../shareds/services/app.service';
import { IActionResultResponse } from '../../../../interfaces/iaction-result-response.result';

@Component({
    selector: 'app-office',
    templateUrl: './office.component.html',
    providers: [OfficeService]
})

export class OfficeComponent extends BaseListComponent<Office> implements OnInit, AfterContentInit {
    @ViewChild(NhTabComponent) tabComponent: NhTabComponent;
    listOffice: Office[] = [];
    status?: number = null;
    isAdmin?: boolean;
    isActiveSearch: boolean;

    private searchTerm = new Subject<string>();

    constructor(@Inject(PAGE_ID) public pageId: IPageId,
                private route: ActivatedRoute,
                private router: Router,
                private title: Title,
                private toastr: ToastrService,
                private spinnerService: SpinnerService,
                private utilService: UtilService,
                private officeService: OfficeService,
                private appService: AppService) {
        super();
    }

    ngOnInit(): void {
        this.appService.setupPage(this.pageId.HR, this.pageId.OFFICE, 'Quản lý phòng ban', 'Danh sách phòng ban');
        const result = this.searchTerm
            .pipe(
                debounceTime(500),
                switchMap(term => this.officeService.search(term, this.isActiveSearch, this.currentPage, this.pageSize)),
                finalize(() => this.spinnerService.hide()));

        result.subscribe((data: any) => {
            this.listOffice = data.items;
            this.totalRows = data.totalRows;
        });

        this.route.data.subscribe((data: { searchResult: any }) => {
            this.listOffice = data.searchResult.items;
            this.totalRows = data.searchResult.totalRows;
        });
    }

    ngAfterContentInit() {
        // setTimeout(() => {
        //     // Bắt đường dẫn để load tab tương ứng.
        //     const url = this.router.url;
        //     if (url.indexOf('edit') > -1) {
        //         this.route.params.subscribe(params => {
        //             const id = params['id'];
        //             this.setUpdate(id);
        //         });
        //     } else if (url.indexOf('add-new') > -1) {
        //         this.addNew();
        //     }
        // }, 100);
    }

    search(currentPage) {
        this.currentPage = currentPage;
        this.isSearching = true;
        this.searchTerm.next(this.keyword);
    }

    searchKeyUp(keyword: string) {
        this.keyword = keyword;
        this.search(1);
    }

    add() {

    }

    edit(officeId: number) {

    }

    delete(office: Office) {
    }

    onChangeActiveStatus(office: Office) {
        office.isActive = !office.isActive;

        this.officeService.updateIsActive(office).subscribe((result: IActionResultResponse) => {
            // if (result === -1) {
            //     this.toastr.error(this.formatString(this.message.notExists, 'Phòng ban'));
            //     return;
            // }
            //
            // if (result > 0) {
            //     this.toastr.success(`${office.isActive ? 'Kích hoạt' : 'Bỏ kích hoạt'} phòng ban "${office.name}" thành công.`);
            //     return;
            // }
            //
            // if (result === 0) {
            //     this.toastr.warning('Vui lòng thay đổi trạng thái của phòng ban');
            //     return;
            // }

            this.toastr.warning(result.message, result.title);
            return;
        });
    }

    onTabClosed(data: { id: string, active: boolean }) {
        if (data.active) {
            this.search(this.currentPage);
            this.tabComponent.setTabActiveById('tabListOffice');
        }
    }

    createRange(number) {
        const items = [];
        for (let i = 1; i <= number; i++) {
            items.push(i);
        }
        return items;
    }
}
